/** @jsx jsx */
import React from "react"
import { Link } from "gatsby"
import { jsx, useThemeUI } from "theme-ui"
import {
  HandbagFill,
  Headset,
  InfoCircleFill,
  ThreeDots,
} from "react-bootstrap-icons"
import { useShopProducts } from "StaticQueries/shop-products"
import getPaymentUrl from "Components/payments/qiwi"
import ColorModeSwitcher from "Components/modules/colorModeSwitcher"
import SearchBar from "Components/modules/searchBar"

export default function IndexPage() {
  const context = useThemeUI()
  const { theme } = context
  const shopProductsData = useShopProducts()

  return (
    <>
      <ColorModeSwitcher
        styles={{
          variant: [
            "element.size.medium",
            "element.type.button",
            "button.primary",
          ],
          position: "absolute",
          top: "0px",
          right: "0px",
        }}
        text="Switch color mode"
      />
      <div
        sx={{
          display: "grid",
          height: "100%",
          gridAutoFlow: "column",
          gridTemplateColumns: "320px auto",
        }}
      >
        {/* HEADER */}
        <div
          id={"header"}
          sx={{
            position: "fixed",
            top: "0px",
            width: "320px",
            backgroundColor: "background_primary",
            boxShadow: `inset -1px 0px 0px ${theme.colors.separator_opaque},inset 0px -1px 0px ${theme.colors.separator_opaque}`,
            zIndex: "1030",
          }}
        >
          <Link
            to="/"
            sx={{
              variant: ["element.size.large"],
              display: "grid",
              textAlign: "center",
              fontWeight: "bold",
              color: "text",
              ":hover": {
                color: "text",
                textDecoration: "none",
              },
            }}
          >
            Site name
          </Link>
          {/* END Sidebar header */}
          {/* START Search */}
          <div
            sx={{
              px: "16px",
              py: "8px",
            }}
          >
            <SearchBar placeholder="Поиск по товарам" elementSize="medium" />
          </div>
        </div>

        {/* FOOTER */}
        <div
          sx={{
            position: "fixed",
            display: "grid",
            gridTemplateColumns: "1fr 1fr 1fr 1fr",
            bottom: "0px",
            width: "320px",
            backgroundColor: "background_primary",
            boxShadow: `inset -1px 0px 0px ${theme.colors.separator_opaque},inset 0px 1px 0px ${theme.colors.separator_opaque}`,
            zIndex: "1030",
          }}
        >
          <button
            data-content_layer_button={"Catalog"}
            sx={{
              variant: "element.type.button",
              display: "grid",
              alignItems: "center",
              justifyItems: "center",
              padding: "4px",
              color: "gray",
              ":hover, :focus": {
                color: "tint_blue",
              },
            }}
          >
            <div sx={{ variant: "iconWrapper.large" }}>
              <HandbagFill size={18} />
            </div>
            <span
              sx={{
                fontSize: "12px",
                lineHeight: "16px",
                marginTop: "4px",
              }}
            >
              Каталог
            </span>
          </button>
          <button
            data-content_layer_button={"Rules"}
            sx={{
              variant: "element.type.button",
              display: "grid",
              alignItems: "center",
              justifyItems: "center",
              padding: "4px",
              color: "gray",
              ":hover, :focus": {
                color: "tint_blue",
              },
            }}
          >
            <div sx={{ variant: "iconWrapper.large" }}>
              <InfoCircleFill size={18} />
            </div>
            <span
              sx={{
                fontSize: "12px",
                lineHeight: "16px",
                marginTop: "4px",
              }}
            >
              Правила
            </span>
          </button>
          <button
            data-content_layer_button={"Help"}
            sx={{
              variant: "element.type.button",
              display: "grid",
              alignItems: "center",
              justifyItems: "center",
              padding: "4px",
              color: "gray",
              ":hover, :focus": {
                color: "tint_blue",
              },
            }}
          >
            <div sx={{ variant: "iconWrapper.large" }}>
              <Headset size={18} />
            </div>
            <span
              sx={{
                fontSize: "12px",
                lineHeight: "16px",
                marginTop: "4px",
              }}
            >
              Помощь
            </span>
          </button>
          <button
            data-content_layer_button={"Menu"}
            sx={{
              variant: "element.type.button",
              display: "grid",
              alignItems: "center",
              justifyItems: "center",
              padding: "4px",
              color: "gray",
              ":hover, :focus": {
                color: "tint_blue",
              },
            }}
          >
            <div sx={{ variant: "iconWrapper.large" }}>
              <ThreeDots size={18} />
            </div>
            <span
              sx={{
                fontSize: "12px",
                lineHeight: "16px",
                marginTop: "4px",
              }}
            >
              Меню
            </span>
          </button>
        </div>

        {/* SIDEBAR */}
        <div
          sx={{
            height: "100%",
            backgroundColor: "background_secondary",
            boxShadow: `inset -1px 0px 0px ${theme.colors.separator_opaque}`,
          }}
        >
          <aside
            data-content_layer_sidebar={"Catalog"}
            sx={{
              position: "fixed",
              width: "320px",
              overflowY: "auto",
              height: "100%",
              scrollbarWidth: "none",
              msOverflowStyle: "none",
              "::-webkit-scrollbar": {
                display: "none",
              },
            }}
          >
            <div
              data-header_compensator={"96px"}
              sx={{ paddingTop: "96px" }}
            ></div>

            {/* START Catalog list */}
            <div sx={{ marginTop: "24px" }}>
              <span
                sx={{
                  variant: ["element.size.small"],
                  color: "gray",
                  textTransform: "uppercase",
                }}
              >
                Каталог товаров
              </span>
              <div
                id="list-example"
                className="list-group"
                sx={{ listStyle: "none", paddingLeft: "0" }}
              >
                <a
                  href="#"
                  sx={{ variant: ["element.size.large", "element.type.row"] }}
                >
                  Личные аккаунты
                </a>
                <a
                  href="#"
                  sx={{ variant: ["element.size.large", "element.type.row"] }}
                >
                  Брут аккаунты
                </a>
              </div>
            </div>
          </aside>
        </div>

        {/* MAIN */}
        <div data-content_layer_page={"Catalog"} sx={{ padding: "32px" }}>
          <h2 sx={{ marginBottom: "24px" }}>Каталог товаров</h2>
          {/* /// */}
          <table class="table">
            <thead>
              <tr>
                <th scope="col">Товар</th>
                <th scope="col">Наличие</th>
                <th colspan="2">Цена за 1 шт.</th>
              </tr>
            </thead>
            <tbody>
              {shopProductsData.map(({ node }) => (
                <tr>
                  <td
                    scope="col"
                    sx={{
                      variant: ["element.size.medium"],
                      display: "table-cell",
                      padding: "0px !important",
                    }}
                  >
                    <Link
                      to={node.slug}
                      sx={{ variant: ["element.size.medium"] }}
                    >
                      {node.excerpt}
                    </Link>
                  </td>
                  <td
                    scope="col"
                    sx={{
                      variant: ["element.size.medium"],
                      display: "table-cell",
                    }}
                  >
                    {node.frontmatter.extra.amount}
                  </td>
                  <td
                    scope="col"
                    sx={{
                      variant: ["element.size.medium"],
                      display: "flex",
                      justifyContent: "space-between",
                      alignItems: "center",
                      padding: "0px !important",
                    }}
                  >
                    <span sx={{ marginRight: "8px" }}>
                      {node.frontmatter.extra.price}
                    </span>
                    <span
                      data-toggle="modal"
                      data-target={`#${node.id}`}
                      sx={{
                        variant: ["element.size.medium"],
                        //display: "table-cell",

                        color: "tint_blue",
                        cursor: "pointer",
                      }}
                    >
                      Купить
                    </span>
                  </td>

                  <div
                    className="modal fade"
                    id={node.id}
                    tabIndex="-1"
                    aria-hidden="true"
                  >
                    <div className="modal-dialog modal-dialog-centered modal-sm">
                      <div className="modal-content">
                        <div
                          sx={{
                            backgroundColor: "background_primary",
                            boxShadow: `inset 1px 0px 0px ${theme.colors.separator_opaque}, inset -1px 0px 0px ${theme.colors.separator_opaque}, inset 0px 1px 0px ${theme.colors.separator_opaque},inset 0px -1px 0px ${theme.colors.separator_opaque}`,
                            borderRadius: `${theme.root.secondaryBorderRadius}`,
                          }}
                        >
                          <div
                            sx={{
                              variant: ["element.size.large"],
                              display: "grid",
                              textAlign: "center",
                              fontWeight: "bold",
                              color: "text",
                              boxShadow: `inset 0px -1px 0px ${theme.colors.separator_opaque}`,
                              ":hover": {
                                color: "text",
                                textDecoration: "none",
                              },
                            }}
                          >
                            Оформить заказ
                          </div>
                          <div sx={{ paddingX: "16px", paddingY: "24px" }}>
                            <input
                              placeholder={"Ваш email"}
                              sx={{
                                variant: [
                                  `element.size.medium`,
                                  "element.type.input",
                                  "input.filled",
                                ],
                                marginBottom: "16px",
                              }}
                            ></input>
                            <span
                              sx={{
                                variant: [`element.size.small`],
                                color: "gray",
                                textTransform: "uppercase",
                              }}
                            >
                              Способ оплаты
                            </span>

                            <select
                              className="form-select"
                              aria-label="Default select example"
                              sx={{
                                variant: [
                                  `element.size.medium`,
                                  "element.type.input",
                                  "input.filled",
                                ],
                                paddingLeft: "12px",
                                color: "text",
                                backgroundImage: "none",
                              }}
                            >
                              <option defaultValue="1">Банковская карта</option>
                              <option value="2">Qiwi</option>
                            </select>
                          </div>
                          <div
                            sx={{
                              display: "grid",
                              gridAutoFlow: "column",
                              boxShadow: `inset 0px 1px 0px ${theme.colors.separator_opaque}`,
                            }}
                          >
                            <button
                              type="button"
                              data-dismiss="modal"
                              aria-label="Close"
                              sx={{
                                variant: [
                                  `element.size.large`,
                                  "element.type.button",
                                ],
                                color: "tint_blue",
                                boxShadow: `inset -1px 0px 0px ${theme.colors.separator_opaque}`,
                              }}
                            >
                              Отмена
                            </button>
                            <button
                              onClick={event => {
                                const paymentInfo = {
                                  amount: node.frontmatter.extra.price,
                                  email: `gjlk@gmail.com`,
                                  comment: `clientCommentHere`,
                                }

                                location.href = getPaymentUrl(paymentInfo)
                              }}
                              sx={{
                                variant: [
                                  `element.size.large`,
                                  "element.type.button",
                                ],
                                color: "tint_blue",
                                fontWeight: "bold",
                              }}
                            >
                              Оплатить
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </tr>
              ))}
            </tbody>
          </table>
          {/* Table*/}
          <div>
            <div
              sx={{
                a: {
                  display: "grid",
                  gridAutoFlow: "column",
                  alignItems: "center",
                  gridTemplateColumns: "1fr  88px 88px",
                  color: "gray",
                  ":hover": {
                    textDecoration: "none",
                    color: "gray",
                  },
                },
                span: {
                  paddingX: "16px",
                  paddingY: "8px",
                  whiteSpace: "nowrap",
                  overflow: "hidden",
                },
                div: {
                  ":nth-child(even)": {
                    backgroundColor: "fill_quarternary",
                  },
                  ":first-child": {
                    boxShadow: `inset 0px -1px 0px ${theme.colors.separator_opaque}`,
                  },
                  ":last-child": {
                    // boxShadow: `inset 1px 0px 0px ${theme.colors.separator_opaque},inset -1px 0px 0px ${theme.colors.separator_opaque},inset 0px -1px 0px ${theme.colors.separator_opaque}`,
                  },
                },
              }}
            ></div>
          </div>
        </div>
      </div>
    </>
  )
}
